import InfiniteScrollLazyLoad from "@components/commons/effect/InfiniteScrollLazyLoadComponent";
import React from "react";
const Banner = React.lazy(() => import("./components/Banner"));
const LoanPartner = React.lazy(() => import("./components/LoanPartner"));
const MoneyPartner = React.lazy(() => import("./components/MoneyPartner"));
const ContractPartner = React.lazy(
  () => import("./components/ContractPartner")
);
const IntroductionPartner = React.lazy(
  () => import("./components/IntroductionPartner")
);

const Dashboard = ({}) => {
  return (
    <div className="dashboard relative">
      <div>
        <React.Suspense fallback={<></>}>
          <Banner />
          <IntroductionPartner />
          <InfiniteScrollLazyLoad delay={300}>
            <LoanPartner />
            <ContractPartner />
            <MoneyPartner />
          </InfiniteScrollLazyLoad>
        </React.Suspense>
      </div>
    </div>
  );
};
export default Dashboard;
